@import url(https://fonts.googleapis.com/css2?family=Oxanium:wght@300;400;500;600;700;800&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Oxanium', cursive;
}

body {
  text-align: center;
  background-image: url(/KantoDex/static/media/bg.cfb36814.png);
  background-position: center;
  background-size: cover;
  min-height: 100vh;
}

.darkMode{
  background-image: url(/KantoDex/static/media/bg-dark.4542575f.PNG);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.div-dark{
    position: absolute;
    display: inline-grid;
    bottom: 15%;
    left: 10%;
    text-align: left;
}
.botao-dark{
    width: 33%;
    padding: 14%;
    border-radius: 100%;
    border-color: transparent;
    background-color: transparent;
    margin-bottom: 0.9vmin;
    margin-left: -5%;
}
.botao-dark:hover{
    cursor: pointer;
}
.text-dark{
    padding: 1px;
    background-color: #E4E4E4;
    border-style: ridge;
    border-radius: 6px;   
    color: darkslategrey;
    font-weight: 500;
    font-size: clamp(0.25px, 2vw, 15px); 
}

main{
    display: inline-block;
    padding-top: 15vmin;
    position: relative;
}

#pokedex{
    width: 100%;
    max-width: 680px;
}

.img-pokemon{
    width: 18%;
    position: absolute;
    bottom: 35.5%;
    left: 19%;
    height: 18%;
}

.dados-pokemon{
    background-color: #E4E4E4;
    border-style: ridge;
    border-radius: 6px;
    padding: 2px;
    position: absolute;
    font-weight: 600;
    color: black;
    bottom: 11%;
    right: 7.5%;
    font-size: clamp(5px, 3.5vw, 20px);
}

.nome-pokemon{
    text-transform: uppercase;
}

form{
    position: absolute;
    width: 42%;
    top: clamp(10%,18vh, 22%);
    right: 2%;
}

.basePokemon{
    position: absolute;
    bottom: 18.5%;
    right: 7.5%;
    background-color: #E4E4E4;
    border-style: ridge;
    border-radius: 6px;
    padding: 2px;
    width: 34%;
    height: 34.5%;
    font-weight: 500;
    font-size: clamp(7px, 2.4vw, 17px);
}
#typesUm {
    margin: 1%;
    width: 28%;
}
#typesDois {
    margin: 1%;
    width: 28%;
}
#statsPokemon{
    padding-left: 20%;
    text-align: left;
}

.input-busca{
    text-align: center;
    width: 100%;
    padding: 2%;
    outline: none;
    border: 2px solid cadetblue;
    border-radius: 5px;
    font-weight: 600;
    color: darkslategrey;
    font-size: clamp(5px, 3vw, 20px);
}

.btn-setas{
    position: absolute;
    display: flex;
    padding-top: 0.30%;
    top: 80%;
    left: 32.4%;
    width: 13.5%;
    grid-gap: 30%;
    gap: 30%;
}
.button{
    width: 45%;
    font-size: clamp(5px, 3vw, 22px);
    font-weight: 600;
    color: #ffff;
    background-color: transparent;
    border-color: transparent;
}
.button:active{
    font-size: 1rem;
}

.text-darkMode{
    background-color: #282828;
    color: #E4E4E4;
}
header{
    position: fixed;
    left: 25%;
    right: 0%;
    top: 4%;
}
  
a {
    color: #fff;
    font-weight: 300;
    text-decoration: none;
    transition: 0.3s;
  }
  
a:hover {
    opacity: 0.7;
  }
  
  nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: transparent;
    height: 12vmin;
    width: 64%;
    
  }
  
  
  .nav-list {
    list-style: none;
    display: flex;
  }
  
  .nav-list li {
    letter-spacing: 3px;
    margin-left: 32px;
    font-size: clamp(8px, 5vw, 20px);
  }
  

  
  
